body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(to bottom, #FF66C4, #FFDE59);
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;
  position: relative;
  
  font-size: 1.2em;
  
  word-break: break-word;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body::after {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('./pina.png');
  background-size: 100px;
  z-index: -1;
}
/*Todo eso rosa que dice cocteles*/
.agrupa{
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: left;
}

#banner{
  position: relative;
  min-height: 100vh;
  width: 15%;
  margin: 0 5%;
  background-color: #F31872;
  border-left: 10px solid #000;
  border-right: 10px solid #000;
}

/*La letra para el letrero*/
@import url('https://fonts.googleapis.com/css2?family=Playwrite+PL:wght@100..400&display=swap');

#letrero{
  color: #FAD02C;
  padding:6% 2%;
  writing-mode: vertical-rl;
  letter-spacing: 20px;
  font-size: 4.5em;
  font-family: "Playwrite PL", cursive;
  font-optical-sizing: auto;
  font-weight: 400;
  -webkit-text-stroke: 15px #000000; 
  word-break:keep-all;
}



#letrero::before,
#letrero::after {
  content: attr(data-text); /* Mostrar el mismo texto */
  position: absolute;
  left: 2%;
  top: 1%;
  -webkit-text-stroke: #FAD02C;
  color:#FAD02C; /* Color de texto más oscuro */
}
#letrero::after {
  left: 2%; /* Ajuste para la segunda duplicación */
  top: 1%;
  color: #FAD02C; /* Color de texto más claro */
}

#banner #linea1,
#banner #linea2,
#banner #linea3 {
  height: 100%;
  position: fixed;
}
#banner #linea1{
  border-left: 10px solid #000;
}
#banner #linea2{
  right:  10%;
  border-left: 6px solid #000;
}
#banner #linea3{
  right:  5%;
  border-left: 4px solid #000;
}

/*Donde debe de estar el contenido*/
#contenido{
  top: 5px;
  width: 60%; 
  position: relative;
  margin-bottom: 2%;
}




#header{
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  margin-bottom: -15px;
}
#contenido #header h1{
  font-family: "Playwrite PL", cursive;
  font-optical-sizing: auto;
  text-align: center;
}
#header img{
  width: 100px;
  height: 130px;
  
}
#menu{
  margin-top: -30px;
}
#menu ul{
  background-color: white;
  padding: 15px 0;
  border-radius: 64px;
  border:#000 2px solid;
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  
}
#menu li{
  font-weight: bold;
  word-break:normal;
}
#menu ul li button{
  background-color: transparent;
  border: none;
  cursor:pointer;
  color: #000;
  transition: color 0.5s ;
  font-size: 1.2em;
}
#menu ul li button:hover{
  color: #F31872;
}
.mensaje{
  background-color: #ffeef1;
  padding: 10px 20px;border-radius: 14px;
  border:#2a1954 2px dashed;
  display: inline-block;
  text-align: justify;
  margin: 5px 0;
  font-weight: 500;
  word-spacing: 1px;
  font-size: 1.1em;
}
.mensajeTitulo{
  background-color: #FFDE59;
  padding: 0 10px;
  border-radius: 14px;
  border:#2a1954 2px dashed;
  display: inline-block;
  margin: 5px 0;
}
.mensajeTitulo h1{
  color: #F31872; 
  font-family: "Playwrite PL", cursive;
  font-optical-sizing: auto;
  text-align: center;
  font-size: 1em;;
  font-weight: 400;
  padding: 0;
  -webkit-text-stroke: 1px #F31872; 
}
.imgMensaje{
  border-radius: 14px;
  border:#2a1954 2px dashed;
  display: block;
  margin: 5px 0;
  width: 50%;
  height: fit-content;
}
.contenedorCoctel{
  width: 100%;
}
.contenedorCoctel button{
  padding: 5px;
  width: 45%;
  margin: 2%;
  background-color: #F31872;
  color: white;
  font-size: 1.2em;
  border-radius: 14px;
  border:#2a1954 2px dashed;
  box-shadow: 5px 5px 0px 0px #2a1954;
-webkit-box-shadow: 5px 5px 0px 0px  #2a1954;
-moz-box-shadow: 5px 5px 0px 0px  #2a1954;
}
.contenedorCoctel button:hover {background-color: #a81f58}

.contenedorCoctel button:active {
  background-color: #a81f58;
  box-shadow: 4px 3px 0px 0px #2a1954;
  transform: translateY(4px);
}
.coctel{
  border-radius: 14px;
  border:#2a1954 2px dashed;
  margin: 4%;
  width: 40%;
  height: 350px;
  background-color: #2a1954;
  text-align: center;
  position: relative;
  display: flex;
  flex-direction: column;
}
.coctel span{
  position: absolute;
  color: #FAD02C;
  z-index: 1; 
  text-align: center;
  font-family: "Playwrite PL", cursive;
  left: 5%;
  bottom: 5%;
  background-color: #a81f58;
  border-radius: 20px;
  padding: 10px;
  border:#2a1954 2px dashed;
  box-shadow: 4px 3px 0px 0px #2a1954;
}
.imgCoctel{
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  border-radius: 14px;
}
.infoCoctel{
  width: calc(100%-30px);
  height: 100%;
  color: white;
  z-index: 2; 
  border-radius: 14px;
  text-align: left;
  padding-left: 30px;
  padding-top: 30px;
  transition: background-color 0.3s ease-out;
}
.infoCoctel:hover{
  background-color: #3c2b66b0;
}

.infoCoctel ul{
  list-style-type: none;
}
.infoCoctel ul li::before{
  content: '⭐';
}

.bebidaAleatoria{
  width: 60%;
  margin:0 20%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  text-align: center;
  border-radius: 14px;
  background-color: #a81f58;
  color: #FAD02C;
  font-size: 1.2em;
  text-transform: capitalize;
  position: relative;
  border:#FAD02C 5px dashed;
}
.bebidaAleatoria h2,h3{
  -webkit-text-stroke:1.5px #000;
}
.bebidaAleatoria img{
  width: 40%;
  border-radius: 14px;
}

.galeria{
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  margin-top: 1%;
  justify-content: center;
}
.imgGaleria{
  width: 300px;
  height: 250px;
  margin: 2px;
}
.galeriaGrande{
  min-width: 100vw;
  min-height: 100vh;
  background-color: #00000088;
  position: fixed;
  z-index: 2;
  overflow: auto;
  top: 0;
  left: 0;
}
.imgGaleriaGrande{
  width: 40vw;
  height: 70vh;
  position: fixed;
  z-index: 3;
  top: 10vh;
    left: 30vw;
    overflow: auto;
}

@media (max-width: 1100px){
  #banner{
    width: 5%;
  }
  #letrero{
    display: none;
  }
  #contenido{
    width: 80%;
  }
}

@media (max-width: 749px){
  #banner{
    display: none;
  }
  #contenido{
    top: 5px;
    width: 100%; 
    position: relative;
    margin: 2%;
  }
  .coctel{
    width: 90%;
  }
  .bebidaAleatoria{
    width: 90%;
    margin:2%;
  }
  .imgGaleriaGrande{
    width: 80vw;
    height: 40vh;
    top: 10vh;
    left: 10vw;
  }
  .imgMensaje{
    width: 60%;
    height: 200px;
  }
  .bebidaAleatoria h2,h3{
    font-size: 0.9em;
    -webkit-text-stroke:0px #000;
  }
  #menu ul{
    padding: 7px 0;
  }
  #menu li{
    word-break:normal;
  }
  #menu ul li button{
    font-size: 0.9em;
  }
  #header{
    align-items: end;
  }
}